
import { SEND_COMMAND_TYPE } from '../actions/general/send_command';
import { CLEAR_COMMAND_TYPE } from '../actions/general/clear_command';

const default_state = {
    pending_command: null,
    commands_history:[]
}

function reducer(state = default_state, action) {


    switch (action.type) {
        case SEND_COMMAND_TYPE:
            let pending_command = action.payload;
            return Object.assign({}, state, {
                pending_command: pending_command,
                commands_history: [...state.commands_history,pending_command],
            })
        case CLEAR_COMMAND_TYPE:
            return Object.assign({}, state, {
                pending_command: null,
            })
        default:
            return state
    }

}

export default reducer;