import Chess from "chess.js";

const coord_letters = ['a', 'b', 'c', 'd', 'e', 'f', 'g', 'h'];
const coord_numbers = ['1', '2', '3', '4', '5', '6', '7', '8'];

let allowed_squares = [];

coord_letters.forEach(letter => {
    coord_numbers.forEach(number => {
        allowed_squares.push(letter + number);
    });
});

const number_candidates_object = {
    '1': ["one"],
    '2': ["too", "to", "two"],
    '3': ["tree", "three",'trip'],
    '4': ["for", "forth", 'four', 'pork'],
    '5': ["five", 'fight'],
    '6': ["six", "sex"],
    '7': ["seven", "heaven"],
    '8': ["eight", "ate", "aid", 'j'],
}

const letter_candidates_object = {
    'a': [],
    'b': ['bee', 'be', 'beat'],
    'c': ['see', 'sea', 'cee', 'ce'],
    'd': ['dee', 'de', 'the', 'thee','deep'],
    'e': ['he', 'in', 'eat', 'heat', 'it'],
    'f': ['if', 'ef', 'of', 'off'],
    'g': ['ge', 'gee'],
    'h': ['age'],
}

const word_candidates_object = {
    'O-O': ['castle king', 'castle king side'],
    'O-O-O': ['castle queen', 'castle queen side'],
    'K': ['king', 'keen','Qing','kin','Ken','kings'],
    'Q': ['queen','queens'],
    'R': ['rock', 'broad', 'brought','rocks','rook', 'rooks', 'root','rooke'],
    'B': ['bishop','push up','bishops'],
    'N': ['knight', 'nite', 'night', 'might', 'nice', 'nike','knights','nights'],
    'x': ['takes', 'taste', 'thanks', 'text', 'take'],
}

const go_candidates_object = {
    'go': ['goat','gold','goal','no'],
}

const final_replacements_object = {
    'a1': ['81', '8 1'],
    'a2': ['82', '8 2'],
    'a3': ['83', '8 3'],
    'a4': ['84', '8 4'],
    'a5': ['85', '8 5'],
    'a6': ['86', '8 6', 'asics'],
    'a7': ['87', '8 7'],
    'a8': ['aa', '88'],
    'b4': ['before'],
    'e4': ['fo'],
    'e8': ['yet'],
    'x e': ['tracy', 'tasty'],
    'R x': ['rockface'],
    'R e': ['rocky'],
    'ax': ['a x'],
    'bx': ['b x'],
    'cx': ['c x'],
    'dx': ['d x'],
    'ex': ['e x'],
    'fx': ['f x'],
    'gx': ['g x'],
    'hx': ['h x'],
}

const homophones = [
    ['xe', 'xc'],
]

class MoveInterpreter {
    constructor(game = null) {
        this.cached_piece = null;
        this.cached_first_square = null;
        this.cached_destination = null;
        this.cached_takes = null;
        this.cached_castle = null;
        this.timeout_id = null;
        this.second_square_allowed = false;
        this.accepted_move = null;
        this.can_clean_cache = true;
        this.possible_squares = [];
        this.game = new Chess();
    }

    on_recognition_change(str) { }

    recognition_change() {
        const castle_str = this.cached_castle ? this.cached_castle : "";
        const piece_str = this.cached_piece ? this.cached_piece : "";
        const takes_str = this.cached_takes ? this.cached_takes : "";
        const first_square_str = this.cached_first_square ? this.cached_first_square : "";
        const destination_str = this.cached_destination ? this.cached_destination : "";
        const str_val = castle_str + piece_str+ first_square_str+ takes_str+ destination_str;
        this.on_recognition_change(str_val);
    }


    start_cleaning_timeout_counter() {
        if (this.timeout_id != null) {
            clearTimeout(this.timeout_id);
        }
        this.timeout_id = setTimeout(() => {
            this.clean_move_cache();
        }, 5000);
    }

    clean_move_cache() {
        if (this.can_clean_cache) {
            console.log('move cache cleaned');
            this.on_recognition_change('---');
            this.can_clean_cache = false;
            this.cached_piece = null;
            this.cached_first_square = null;
            this.cached_destination = null;
            this.cached_takes = null;
            this.cached_castle = null;
            this.second_square_allowed = false;
            this.accepted_move = null;
            this.possible_squares = [];
            setTimeout(() => {
                this.can_clean_cache = true;
            }, 1000)
        }
    }

    recognize_takes(final_str) {
            if (final_str.includes('x')) {
                this.cached_takes = 'x';
            }
    }

    recognize_castle(final_str) {
        if (final_str.includes('O-O-O')) {
            this.cached_castle = 'O-O-O';
            return true;
        } else if (final_str.includes('O-O')) {
            this.cached_castle = 'O-O';
            return true;
        }
        return false;
    }

    recognize_piece(final_str) {
        // if (this.cached_piece !== null) return;
        const allowed_pieces = ['K', 'Q', 'R', 'B', 'N'];
        const piece_letters = final_str.split("");

        for (let i = 0; i < piece_letters.length; i++) {
            const letter = piece_letters[i];
            if (allowed_pieces.includes(letter)) {
                this.cached_piece = letter;
                break;
            }
        }
    }

    recognize_squares(final_str) {
        if (this.cached_destination !== null) return;
        // console.log('recognizing squares for ' + final_str);
        let allowed_squares_for_recognition = [...coord_letters, ...allowed_squares];
        if (this.cached_first_square === null) {
            for (let i = 0; i < allowed_squares_for_recognition.length; i++) {
                const square = allowed_squares_for_recognition[i];
                if (final_str.includes(square)) {
                    if (!this.possible_squares.includes(square)) {
                        this.possible_squares.push(square);
                    }
                }
            }
        }
    }

    get_recognition_state() {
        const piece_str = this.cached_piece ? this.cached_piece : "";
        const takes_str = this.cached_takes ? this.cached_takes : "";
        return piece_str+takes_str;
        // return JSON.stringify([
        //     this.cached_castle,
        //     this.cached_destination,
        //     this.cached_first_square,
        //     this.cached_piece,
        //     this.cached_takes,
        // ]);
    }


    recognize_features(final_str) {
        const state_before = this.get_recognition_state();
        if (this.recognize_castle(final_str)) {
            return;
        }
        this.recognize_piece(final_str);
        this.recognize_squares(final_str);
        this.recognize_takes(final_str);
        const state_after = this.get_recognition_state();
        // console.log('comparing ' + state_before + ' with ' + state_after);

        if (state_before !== state_after) {
            this.recognition_change();
        }
    }

    create_move(piece, first_square, takes, destination) {
        if (!destination) {
            return null;
        }
        let piece_str = piece ? piece : "";
        let first_square_str = first_square ? first_square : "";
        let takes_str = takes ? takes : "";
        let destination_str = destination ? destination : "";
        return piece_str + first_square_str + takes_str + destination_str;
    }

    get_homophone_options(move) {
        let options = [];
        let options_found = false;
        for (let i = 0; i < homophones.length; i++) {
            const homophones_list = homophones[i];
            for (let j = 0; j < homophones_list.length; j++) {
                const word = homophones_list[j];
                if (move.includes(word)) {
                    for (let k = 0; k < homophones_list.length; k++) {
                        const replacement_candidate = homophones_list[k];
                        options.push(move.split(word).join(replacement_candidate));
                        options_found = true;
                    }
                }
            }
        }
        if (options_found) {
            return options;
        }
        return [move];
    }

    get_legal_homophone(move_option) {
        console.log('received option for homophones:', move_option);
        let homophones = this.get_homophone_options(move_option);
        for (let i = 0; i < homophones.length; i++) {
            const option = homophones[i];
            if (this.is_legal_move(option)) {
                return option;
            } else {
                console.log(option, 'not legal');

            }
        }
        return null;
    }

    get_legal_move() {
        // console.log('possible sqars:', this.possible_squares);

        if (this.cached_castle) {
            if(this.is_legal_move(this.cached_castle))
            return this.cached_castle;
        }

        let two_letter_squares = this.possible_squares.filter(square => (square.length === 2));
        // console.log('two letter sqares:', two_letter_squares);
        //try for only destination square
        for (let i = 0; i < two_letter_squares.length; i++) {
            const square = two_letter_squares[i];
            let preliminar_option = this.create_move(this.cached_piece, null, this.cached_takes, square);
            let final_option = this.get_legal_homophone(preliminar_option);
            if (final_option !== null) return final_option;
        }

        let only_letter_squares = this.possible_squares.filter(square => (square.length === 1));
        // console.log('only letter sqares:', only_letter_squares);
        //try for only letter source, two letter destination
        for (let i = 0; i < only_letter_squares.length; i++) {
            const source_square = only_letter_squares[i];
            for (let j = 0; j < two_letter_squares.length; j++) {
                const destination = two_letter_squares[j];
                let preliminar_option = this.create_move(this.cached_piece, source_square, this.cached_takes, destination);
                let final_option = this.get_legal_homophone(preliminar_option);
                if (final_option !== null) return final_option;
            }
        }

        //try for two letter source, two letter destination
        for (let i = 0; i < two_letter_squares.length; i++) {
            const source_square = two_letter_squares[i];
            for (let j = 0; j < two_letter_squares.length; j++) {
                const destination = two_letter_squares[j];
                if (source_square !== destination) {
                    let preliminar_option = this.create_move(this.cached_piece, source_square, this.cached_takes, destination);
                    let final_option = this.get_legal_homophone(preliminar_option);
                    if (final_option !== null) return final_option;
                }
            }
        }
        return null;

    }

    set_game_position(fen_position) {
        if (fen_position) {
            this.game.load(fen_position);
        }
    }

    is_legal_move(move) {
        const move_result = this.game.move(move);
        if (move_result !== null) {
            this.game.undo();
            return true;
        }
        return false;
    }

    detect_go(str_val) {
        let final_str = str_val;
        final_str = this.replace_go(final_str);
        if (final_str.includes('go')) {
            // console.log('go detected');

            this.clean_move_cache();
            final_str = final_str.replace('go', '');
        }
        return final_str
    }

    interpret(str_val) {
        let final_str = str_val.toLowerCase();
        // console.log('possible_move_string after toLowerCase:', final_str);
        final_str = this.detect_go(final_str);
        final_str = this.get_rid_of_signs(final_str);
        // console.log('possible_move_string after get_rid_of_signs:', final_str);
        final_str = this.separate_numbers(final_str);
        // console.log('possible_move_string after separate_numbers:', final_str);
        final_str = " " + final_str + " ";
        // console.log('possible_move_string after add spaces:', final_str);
        final_str = this.replace_words(final_str);
        // console.log('possible_move_string after replace_words:', final_str);
        final_str = this.replace_numbers(final_str);
        // console.log('possible_move_string after replace_numbers:', final_str);
        final_str = this.replace_letters(final_str);
        // console.log('possible_move_string after replace_letters:', final_str);
        final_str = this.replace_general(final_str);
        // console.log('possible_move_string after replace_general:', final_str);
        final_str = this.remove_remaining(final_str);
        // console.log('possible_move_string after remove_remaining:', final_str);
        final_str = this.remove_spaces(final_str);
        // console.log('possible_move_string after remove_spaces:', final_str);
        final_str = this.replace_general(final_str);
        console.log('possible_move:', final_str);
        this.recognize_features(final_str);
        this.start_cleaning_timeout_counter();
        final_str = this.get_legal_move(final_str);
        if (final_str !== null) {
            if (this.accepted_move === final_str) {
                return null;
            }else{
                this.accepted_move = final_str;
            }
            this.on_recognition_change(final_str);
            final_str = 'move:' + final_str;
            console.log(final_str);

        }
        return final_str
    }

    get_rid_of_signs(str_val) {
        const signs = ['-', '.', ',', '?', ';', ':'];
        let final_str = str_val;
        for (let i = 0; i < signs.length; i++) {
            const sign = signs[i];
            final_str = final_str.split(sign).join("");
        }
        return final_str;
    }

    replace_all(str_val, search, replace_val) {
        const alias_regexp = new RegExp('\\b' + search + '\\b', 'g')
        return str_val.replace(alias_regexp, replace_val);
    }

    replace_words(str_val) {
        let new_str = str_val;
        const dict = word_candidates_object;
        const keys = Object.keys(dict);
        keys.forEach(key => {
            const alias_array = dict[key];
            alias_array.forEach(alias => {
                new_str = this.replace_all(new_str, alias, key);
            });
        });
        return new_str;
    }

    replace_general(str_val) {
        let new_str = str_val;
        const dict = final_replacements_object;
        const keys = Object.keys(dict);
        keys.forEach(key => {
            const alias_array = dict[key];
            alias_array.forEach(alias => {
                new_str = new_str.split(alias).join(key);
            });
        });
        return new_str;
    }

    separate_numbers(str_val) {
        let final_str = str_val;
        final_str = final_str.split("1").join(" 1");
        final_str = final_str.split("2").join(" 2");
        final_str = final_str.split("3").join(" 3");
        final_str = final_str.split("4").join(" 4");
        final_str = final_str.split("5").join(" 5");
        final_str = final_str.split("6").join(" 6");
        final_str = final_str.split("7").join(" 7");
        final_str = final_str.split("8").join(" 8");
        return final_str;
    }

    replace_numbers(str_val) {
        let new_str = str_val;
        const dict = number_candidates_object;
        const keys = Object.keys(dict);
        keys.forEach(key => {
            const alias_array = dict[key];
            alias_array.forEach(alias => {
                new_str = this.replace_all(new_str, alias, key);
            });
        });
        return new_str;
    }

    replace_letters(str_val) {
        let new_str = str_val;
        const dict = letter_candidates_object;
        const keys = Object.keys(dict);
        keys.forEach(key => {
            const alias_array = dict[key];
            alias_array.forEach(alias => {
                new_str = this.replace_all(new_str, alias, key);
            });
        });
        return new_str;
    }
    replace_go(str_val) {
        let new_str = str_val;
        const dict = go_candidates_object;
        const keys = Object.keys(dict);
        keys.forEach(key => {
            const alias_array = dict[key];
            alias_array.forEach(alias => {
                new_str = this.replace_all(new_str, alias, key);
            });
        });
        return new_str;
    }

    is_coordinate(word) {
        if (word.length === 2) {
            if (coord_letters.includes(word[0]) && coord_numbers.includes(word[1])) {
                return true;
            }
        }
        return false;
    }

    remove_remaining(str_val) {
        const allowed_words = [
            ...['ax', 'bx', 'cx', 'dx', 'ex', 'fx', 'gx', 'hx',],
            ...Object.keys(letter_candidates_object),
            ...Object.keys(number_candidates_object),
            ...Object.keys(word_candidates_object),
        ]
        let final_words_arr = [];
        const words_arr = str_val.split(" ");
        words_arr.forEach(word => {
            if (allowed_words.includes(word) || this.is_coordinate(word)) {
                final_words_arr.push(word);
            }
        });
        return final_words_arr.join(" ");
    }

    remove_spaces(str_val) {
        return str_val.split(" ").join("");
    }
}


// module.exports = MoveInterpreter;
export default MoveInterpreter;