import { ADD_PENDING_SENTENCE_TYPE } from '../actions/voice_interface/add_pending_sentence';
import { REMOVE_PENDING_SENTENCE_TYPE } from '../actions/voice_interface/remove_pending_sentence';
import { CLEAR_PENDING_SENTENCES_TYPE } from '../actions/voice_interface/clear_pending_sentences';

const default_state = {
    pending_sentences: [],
}

function reducer(state = default_state, action) {

    switch (action.type) {
        case ADD_PENDING_SENTENCE_TYPE:
            let sentence = action.payload;
            console.log('adding pending sentence: ',sentence);
            console.log('sentences before adding: ',state.pending_sentences);
            var new_pending_sentences = [...state.pending_sentences];
            if(!new_pending_sentences.includes(sentence)){
                new_pending_sentences.push(sentence);
            }
            return Object.assign({}, state, {
                pending_sentences: new_pending_sentences,
            })
        case REMOVE_PENDING_SENTENCE_TYPE:
            let sentence_id = action.payload;
            return Object.assign({}, state, {
                pending_sentences: state.pending_sentences.slice(sentence_id,1),
            })
        case CLEAR_PENDING_SENTENCES_TYPE:
            return Object.assign({}, state, {
                pending_sentences: [],
            })
        default:
            return state;
    }

}

export default reducer;