import Chess from "chess.js";

class GameHandler {

    constructor() {
        this.game = new Chess();
        this.last_move = null;
        this.player_types = {
            'w': 'human',
            'b': 'computer'
        };
        const STOCKFISH = window.STOCKFISH;
        this.engine =
            typeof STOCKFISH === "function"
                ? STOCKFISH()
                : new Worker("stockfish.js");
        this.engine_ready = false;
        this.depth = 2;
        this.engine.onmessage = this.on_engine_message.bind(this);
    }

    on_success_move(move_string) { }
    on_check(p) { }
    on_checkmate(p) { }
    on_win(p) { }
    on_draw() { }
    on_stalemate() { }
    on_insufficient_material() { }
    on_repetition() { }
    on_illegal_move(move_string) { }
    on_opponent_move(move_string) { }

    set_depth(depth) {
        this.depth = depth;
    }

    new_game() {
        this.game.reset();
    }

    load_position(fen_position) {
        this.game.load(fen_position);
    }

    get_side_to_move() {
        let s = this.game.turn();
        if (s === 'w') return 'white';
        if (s === 'b') return 'black';
    }

    set_player_side(side) {
        if (side === 'black') {
            this.player_types.b = 'human';
            this.player_types.w = 'computer';
        } else if (side === 'black') {
            this.player_types.b = 'computer';
            this.player_types.w = 'human';
        }
        this.check_computer_response();
    }

    start_engine() {
        console.log('starting engine');
        if (!this.engine_ready) {
            setTimeout(function () {
                this.send_engine_message('uci');
                this.setTimeout(function () {
                    this.start_engine();
                }.bind(this), 100)
            }.bind(this), 100);
        }
    }

    send_engine_message(messge) {
        this.engine.postMessage(messge);
    }

    on_engine_message(event) {
        let message;

        if (event && typeof event === "object") {
            message = event.data.trim();
        } else {
            message = event.trim();
        }
        console.log('gamehandler engine: ' + message);
        if (message === 'uciok') {
            this.engine_ready = true;
        }
        if (message.split(" ")[0] === 'bestmove') {
            let match = message.match(/^bestmove ([a-h][1-8])([a-h][1-8])([qrbn])?/);
            if (match) {
                // isEngineRunning = false;
                let move_success = this.game.move({ from: match[1], to: match[2], promotion: match[3] });
                let last_move = this.game.history().slice(-1)[0];

                if (move_success) {
                    this.game.undo();
                    console.log('undo correct');
                }
                console.log({ last_move });
                move_success = this.try_move(last_move);
                if (move_success) {
                    this.on_opponent_move(last_move);
                } else {
                    console.log('gameHandler.try_move was unsuccessful for move:', last_move);
                    console.log('game moves:', this.game.history());
                }
            }
        }
    }

    request_engine_move() {
        const position_message = 'position fen ' + this.game.fen();
        const go_message = 'go  wtime 3000 winc 1500 btime 3000 binc 1500 depth ' + this.depth.toString();
        this.send_engine_message(position_message);
        this.send_engine_message(go_message);
    }

    check_position_events() {
        if (this.game.in_draw())
            this.on_draw();
        if (this.game.insufficient_material())
            this.on_insufficient_material();
        if (this.game.in_threefold_repetition())
            this.on_repetition();
        if (this.game.in_stalemate())
            this.on_stalemate();
        if (this.game.in_check())
            this.on_check(this.game.turn());
        if (this.game.in_checkmate())
            this.on_checkmate(this.game.turn());
        this.on_win(this.game.turn());
    }

    check_computer_response() {
        if (this.player_types[this.game.turn()] === 'computer') {
            this.request_engine_move();
        }
    }

    try_move(move_string) {
        console.log('gameHandler: ', move_string);

        let move = this.game.move(move_string);

        if (move === null) {
            console.log(move_string + ' is illegal');
            this.on_illegal_move(move_string);
            return false;
        }
        this.last_move = move_string;
        this.on_success_move(move_string);
        this.check_position_events();
        this.check_computer_response();
        return true;
    }
}

export default GameHandler;