import { createStore, combineReducers } from 'redux';
import game_reducer from './reducers/game_reducer';
import general_reducer from './reducers/general_reducer';
import voice_interface_reducer from './reducers/voice_interface_reducer';
var main_reducer = combineReducers({
    game: game_reducer,
    general: general_reducer,
    voice_interface: voice_interface_reducer,
});

var store = createStore(
    main_reducer,
    window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__()
)
export default store;