import React, { Component } from 'react';
import { connect } from 'react-redux';
import Speaker from './speaker';
import voiceCommandsListener from './voiceCommandsListener';
import UICommandsInterpreter from './UICommandsInterpreter';
import MoveInterpreter from './moveInterpreter';
import CommandsHandler from './commandsHandler';
import MoveToTextConverter from './moveToTextConverter'
import try_move from '../../redux/actions/game/try_move';
import send_command from '../../redux/actions/general/send_command';
import add_pending_sentence from '../../redux/actions/voice_interface/add_pending_sentence';
import clear_sentences from '../../redux/actions/voice_interface/clear_pending_sentences';
import { ReactComponent as MicIcon } from './media/mic.svg';
import Swal from 'sweetalert2';
import './styles/style.css';
const failure_sound = require('./media/failure.mp3');
const success_sound = require('./media/success.wav');
class VoiceInterface extends Component {
    state = {}

    constructor(props) {
        super(props);
        this.can_speak = false;
        this.can_listen = false;
        this.coord_listener = new voiceCommandsListener();
        this.move_interpreter = new MoveInterpreter();
        this.move_converter = new MoveToTextConverter();
        this.move_interpreter.on_recognition_change = this.on_recognition_change.bind(this);
        this.ui_interpreter = new UICommandsInterpreter();
        this.coord_listener.add_interpreter(this.move_interpreter);
        this.coord_listener.add_interpreter(this.ui_interpreter);
        this.commands_handler = new CommandsHandler();
        this.coord_listener.on_received_command = this.commands_handler.execute_command.bind(this.commands_handler);
        this.speaker = new Speaker();
        this.commands_handler.on_move_save = this.on_move_save.bind(this);
        this.commands_handler.on_move = this.on_move.bind(this);
        this.commands_handler.on_command_success = this.command_success.bind(this);
        this.commands_handler.on_command_failure = this.command_failure.bind(this);
        this.commands_handler.on_rejected_move = this.move_interpreter.clean_move_cache.bind(this.move_interpreter);
        this.commands_handler.on_not_recognized_command = this.on_not_recognized_command.bind(this);
        this.coord_listener.on_result = this.on_result.bind(this);
        this.coord_listener.on_read_success = this.command_success.bind(this);
        this.coord_listener.on_end = () => {
            // document.body.style.background = 'red';
            this.refs.mic_icon.classList.add('inactive');
            this.refs.mic_icon.classList.remove('active');
        };
        this.coord_listener.on_start = () => {
            this.refs.mic_icon.classList.add('active');
            this.refs.mic_icon.classList.remove('inactive');
            // document.body.style.background = 'white';
        };
        this.speaker.on_speak_start = this.stop_listening.bind(this);
        this.speaker.on_speak_end = this.listen.bind(this);
    }

    add_sentence(sentence) {
        this.props.add_voice_sentence(sentence);
    }

    on_recognition_change(move_str = "") {
        const font_replacements = {
            K: '<span class="chessfont">k</span>',
            N: '<span class="chessfont">n</span>',
            B: '<span class="chessfont">b</span>',
            R: '<span class="chessfont">r</span>',
            Q: '<span class="chessfont">q</span>',
        }
        let new_str = move_str;
        Object.keys(font_replacements).forEach(piece => {
            // console.log('replacing chess font ',piece,' for ',font_replacements[piece]);            
            new_str = new_str.split(piece).join(font_replacements[piece]);
        });
        console.log('resutl for chess font: ', new_str);

        this.refs.move.innerHTML = new_str;
    }

    on_result(sentence) {
        // console.log('====================================');
        // console.log(this);
        // console.log('====================================');
        // this.refs.output.innerHTML = sentence;
    }


    on_move_save(move_str) {
        const piece = this.move_converter.get_piece(move_str);
        const destination = this.move_converter.get_destination(move_str);
        const first_square = this.move_converter.get_first_square(move_str);
        const takes = this.move_converter.get_takes(move_str);
        const your = takes !== "" ? " " : " your ";
        const to = takes !== "" ? " takes " : " to ";
        this.add_sentence('do you want to move ' +
            your +
            piece + ' ' +
            first_square +
            to +
            destination + '?');
    }

    on_move(move_str) {
        this.props.move(move_str);
        this.add_sentence('done');
    }

    stop_listening() {
        console.log("stopped listening");
        this.coord_listener.stop_listening();
        // document.getElementById('ear-icon').style.display = "none";
    }


    listen() {
        if (this.can_listen) {
            console.log("started listening");
            this.coord_listener.listen();
        }
        // setTimeout(() => {
        //     this.coord_listener.listen();
        // }, 3000);
        // document.getElementById('ear-icon').style.display = "block";
    }

    command_success() {
        try {
            document.getElementById('success-audio').play();
        } catch (error) {

        }
    }
    on_not_recognized_command(command) {
        this.props.send_command(command);
    }
    command_failure(command) {
    }

    componentDidMount() {
        document.body.onclick = function () {
            if (this.can_listen) {
                this.coord_listener.start();
            }
            this.can_speak = true;
            // this.can_listen = true;
        }.bind(this);
        document.addEventListener('keypress', event => {
            if (event.code === 'Space') {
                if (this.can_listen) {
                    this.coord_listener.start();
                }
                this.can_speak = true;
            }
        });
        document.getElementById('commands-trigger').addEventListener('click', function (ev) {
            ev.stopPropagation();
            Swal.fire('Commands',
                "<div>show board: show game board for five seconds</div>" +
                "<div>new game: start a new game</div>" +
                "<div>repeat:repeat last move</div>" +
                "<div></div>"
            );
            document.getElementsByClassName("swal2-container")[0].addEventListener('click', function (ev) {
                ev.stopPropagation();
            });
        });

    }

    set_chess_engine(chess_instance) {
        this.move_interpreter.replace_game_handler(chess_instance);
    }

    check_pending_sentences() {
        if (!this.speaker.is_ispeaking && this.can_speak) {
            this.speak_pending_sentences();
        } else {
            setTimeout(() => {
                this.check_pending_sentences();
            }, 1000);
        }
    }

    componentDidUpdate(prevProps, prevState) {
        // console.log('voice interface updated');
        this.check_pending_sentences();
        this.move_interpreter.set_game_position(this.props.fen_position);
    }

    sentences_spoken() {
        this.speak_pending_sentences();
    }

    async speak_pending_sentences() {
        // console.log('speaking pending sentences:', this.props.pending_sentences);
        this.coord_listener.stop_listening();
        this.can_listen = false;
        if (this.props.pending_sentences.length > 0) {
            const pending_sentences = this.props.pending_sentences;
            this.props.clear_sentences();
            for (let i = 0; i < pending_sentences.length; i++) {
                const sentence = pending_sentences[i];
                await this.speaker.speak(sentence);
            }
            this.sentences_spoken();
        }
        this.can_listen = true;
        // this.coord_listener.listen();
    }


    render() {

        return (


            <div id="voice-interface">
                <h1 ref="output">   V-CHESS</h1>
                <h4 ref="output">voice controlled chess game</h4>
                <MicIcon ref="mic_icon" className="mic-icon inactive" />
                <h1 ref="move">
                    ---
                </h1>
                {/* <img id="ear-icon" src={ear_img} alt="someimg" /> */}
                <audio
                    id="success-audio"
                    src={success_sound}>
                    Your browser does not support the
                    <code>audio</code> element.
                </audio>
                <audio
                    id="failure-audio"
                    src={failure_sound}>
                    Your browser does not support the
                    <code>audio</code> element.
                </audio>
                <p className="hints"></p>
                <div>
                    <p className="touch message"><em>touch the screen or press spacebar to talk</em></p>
                    <p>developer contact: eidiazcas@gmail.com</p>
                </div>
                <div>
                    <button className="btn btn-outline-secondary" id="commands-trigger">Voice commands list</button>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        fen_position: state.game.fen_position,
        pending_sentences: state.voice_interface.pending_sentences,
    };
};

const mapDispatchToProps = (dispatch) => (
    {
        move: (move_str) => {
            dispatch(try_move(move_str));
        },
        add_voice_sentence: (sentence) => {
            dispatch(add_pending_sentence(sentence));
        },
        clear_sentences: () => {
            dispatch(clear_sentences());
        },
        send_command: (command) => {
            dispatch(send_command(command));
        },
    }
);

export default connect(mapStateToProps, mapDispatchToProps)(VoiceInterface);