var SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
var SpeechGrammarList = window.SpeechGrammarList || window.webkitSpeechGrammarList;
// var SpeechRecognitionEvent = window.SpeechRecognitionEvent || window.webkitSpeechRecognitionEvent

// var colors = [ 'aqua' , 'azure' , 'beige', 'bisque', 'black', 'blue', 'brown', 'chocolate', 'coral', 'crimson', 'cyan', 'fuchsia', 'ghostwhite', 'gold', 'goldenrod', 'gray', 'green', 'indigo', 'ivory', 'khaki', 'lavender', 'lime', 'linen', 'magenta', 'maroon', 'moccasin', 'navy', 'olive', 'orange', 'orchid', 'peru', 'pink', 'plum', 'purple', 'red', 'salmon', 'sienna', 'silver', 'snow', 'tan', 'teal', 'thistle', 'tomato', 'turquoise', 'violet', 'white', 'yellow'];
var grammar = "#JSGF V1.0; "+
"grammar coords; "+
"public <letterweights> = /150/ B | /0/ beet | /0/ beat;"+
"public <coordletter> = A | B | C | D | E | F | G | H | a | b | c | d | e | f | g | h;"+
"public <coordnumber> = 1 | 2 | 3 | 4 | 5 | 6 | 7 | 8;"+
"public <coord> = <coordletter> <coordnumber>;"+
"public <coords> = A1 | B1 | C1 | D1 | E1 | F1 | G1 | H1 | A2 | B2 | C2 | D2 | E2 | F2 | G2 | H2 | A3 | B3 | C3 | D3 | E3 | F3 | G3 | H3 | A4 | B4 | C4 | D4 | E4 | F4 | G4 | H4 | A5 | B5 | C5 | D5 | E5 | F5 | G5 | H5 | A6 | B6 | C6 | D6 | E6 | F6 | G6 | H6 | A7 | B7 | C7 | D7 | E7 | F7 | G7 | H7 | A8 | B8 | C8 | D8 | E8 | F8 | G8 | H8 | <coord>;"+
"public <move> = <coords> <coords>;"+
"public <beerule> = /80/ B8 | /0/ V8;"

class VoiceCommandsListener {
    constructor() {
        this.recently_received_command = null;
        this.interpreters_list = [];
        this.recognition = new SpeechRecognition();
        this.speechRecognitionList = new SpeechGrammarList();
        this.speechRecognitionList.addFromString(grammar, 1);
        this.recognition.grammars = this.speechRecognitionList;
        this.recognition.continuous = true;
        this.recognition.lang = 'en-US';
        this.recognition.interimResults = true;
        this.recognition.maxAlternatives = 5;
        this.last_command = "";
        this.command_found = false;
        this.current_command = "";
        this.candidate_word = "";
        this.current_confidence = 0;
        this.is_listening = true;
        this.recognition.onresult = this.process_result.bind(this);
        this.recognition.onspeechend = function () {
            // this.recognition.stop();
            // this.recognition.start();
        };

        this.recognition.onend = function (event) {
            this.on_end();
            // this.start(); 
        }.bind(this);
        this.recognition.onstart = function (event) {
            this.on_start();
        }.bind(this);

        this.recognition.onnomatch = function (event) {
            // alert('no match found');
            this.start();
        }.bind(this);

        this.recognition.onerror = function (event) {
            // event.preventDefault();
            // alert('recognition error');
        };

    }
    on_result(result){}
    on_end() { }
    on_start() { }
    on_read_success() { }
    on_read_failure() { }
    on_received_command(command) { }

    delay_same_command(command){
        this.recently_received_command = command;
        setTimeout(() => {
            this.recently_received_command = null;
        }, 5000);
    }

    add_interpreter(interpreter) {
        this.interpreters_list.push(interpreter);
    }

    stop_listening() {
        // this.is_listening = false;
        this.recognition.stop();
    }

    listen() {
        // this.is_listening = true;
        try {
            this.recognition.start();
        } catch (error) {
            
        }
    }

    start() {
        try {
            // this.recognition.stop();
            this.recognition.start();
        } catch (error) {
            // alert('already started');
        }
    }

    process_result(evt) {
        if (!this.is_listening) {
            evt.preventDefault();
            console.log('not listening');
            return;
        }
        // var word = event.results[0][0].transcript;
        console.log('on result');
        const results_object = evt.results[evt.results.length - 1];
        const alternatives = Array.from(results_object);

        for (let i = 0; i < alternatives.length; i++) {
            const result = alternatives[i];
            const recognized_uninterpreted = result.transcript;
            this.on_result(recognized_uninterpreted);
            for (let j = 0; j < this.interpreters_list.length; j++) {
                const interpreter = this.interpreters_list[j];
                let interpreted_result = interpreter.interpret(recognized_uninterpreted);
                if (interpreted_result !== null) {
                    console.log("interpreted_result:",interpreted_result);
                    if(interpreted_result != this.recently_received_command){
                        this.delay_same_command(interpreted_result);               
                        this.on_received_command(interpreted_result);
                        this.read_success();
                    }
                    return;
                }
            }
        }
        this.read_failure();
    }

    read_success() {
        if (this.is_listening) this.on_read_success();
    }

    read_failure() {
        if (this.is_listening) this.on_read_failure();
    }
}
export default VoiceCommandsListener;









