

class CommandsHandler {
    constructor() {
        this.pending_move = null;
    }
    on_not_recognized_command(command) { }
    on_rejected_move() { }
    on_command_success(command) { }
    on_command_failure(command) { }
    on_move(move_str) { }
    on_move_save(move_str) { }

    is_move(word) {

        if (word.includes('move:')) {
            console.log('====================================');
            console.log(word + ' is move');
            console.log('====================================');
            return true;
        }
        //TODO: implement this
        //it needs to recognize the form returned by move interpreter
        return false;
    }

    get_move(word) {

        let returned = word;
        let returned_arr = returned.split("move:");
        if (returned_arr.length > 1) {
            return returned_arr[1];
        }
        return "";

    }

    is_move_ready(move_str) {
        if (this.pending_move !== null) {
            return true;
        }
        return false;
    }

    save_pending_move(move_str) {
        this.pending_move = move_str;
        this.on_move_save(move_str);
    }

    clear_pending_move() {
        this.pending_move = null;
    }

    execute_command(command) {
        console.log('readed:' + command);
        switch (command) {
            case 'yes':
                if (this.is_move_ready()) {
                    this.on_move(this.pending_move);
                    this.clear_pending_move();
                }
                break;

            case 'no':
                if (this.is_move_ready()) {
                    console.log('rejected move');
                    this.on_rejected_move();
                }
                break;

            default:
                // console.log(this);

                if (this.is_move(command)) {
                    this.save_pending_move(this.get_move(command));
                    break;
                }
                if (command) {
                    //TODO: fix this way of handling commands, its horrible
                    this.on_not_recognized_command(command)
                }

                this.on_command_failure(command);
                return;
        }
        this.on_command_success(command);
    }
}

export default CommandsHandler;