import React, { Component } from 'react';
import { connect } from 'react-redux';
import GameHandler from './gameHandler';
import try_move from '../../redux/actions/game/try_move';
import clear_pending_move from '../../redux/actions/game/clear_pending_move';
import clear_pending_command from '../../redux/actions/general/clear_command';
import set_position from '../../redux/actions/game/set_position';
import set_player_side from '../../redux/actions/game/set_player_side';
import add_pending_sentence from '../../redux/actions/voice_interface/add_pending_sentence';
import MoveToTextConverter from '../voice_interface/moveToTextConverter';
import Chessboard from "chessboardjsx";
import './style.css';

class GameHandlerComponent extends Component {

    constructor(props) {
        super(props);
        this.state = {};
        this.game_handler = new GameHandler();
        this.move_converter = new MoveToTextConverter();
        this.game_handler.on_success_move = (c1, c2) => {
            this.props.set_position(this.game_handler.game.fen());
        }
        this.game_handler.on_illegal_move = (move_string) => {
            console.log('move ' + move_string + ' is illegal');
        }
        this.game_handler.on_checkmate = this.on_checkmate.bind(this);
        this.game_handler.on_repetition = this.on_repetition.bind(this);
        this.game_handler.on_insufficient_material = this.on_insufficient_material.bind(this);
        this.game_handler.on_opponent_move = (move_str) => {
            const piece = this.move_converter.get_piece(move_str);
            const first_square = this.move_converter.get_first_square(move_str);
            const destination = this.move_converter.get_destination(move_str);
            const takes = this.move_converter.get_takes(move_str);
            const his = takes !== "" ? "" : " his ";
            const to = takes !== "" ? " takes " : " to ";
            this.props.add_voice_sentence('your opponent has moved ' +
                his +
                piece +
                first_square +
                to +
                destination);
        }
    }

    on_checkmate(turn){
        if(turn=="w"){
            this.props.add_voice_sentence("chekmate, black wins");
        }else{
            this.props.add_voice_sentence("chekmate, white wins");
        }
    }
    
    on_repetition(){
        this.props.add_voice_sentence("Draw by threefold repetition");
    }
    on_insufficient_material(){
        this.props.add_voice_sentence("Draw by insufficient material");
    }

    repeat() {
        let move = this.game_handler.last_move
        if (move) {
            const piece = this.move_converter.get_piece(move);
            const first_square = this.move_converter.get_first_square(move);
            const destination = this.move_converter.get_destination(move);
            const takes = this.move_converter.get_takes(move);
            this.props.add_voice_sentence(
                piece + ' ' +
                first_square + ' ' +
                takes + ' ' +
                destination +
                '. ' +
                this.game_handler.get_side_to_move() +
                ' to move'
            );
        }else if (!this.props.player_side){
            this.props.add_voice_sentence('Do you want to play white? or black');
        }else{
            this.props.add_voice_sentence('starting position. white to move');
        }
    }

    componentDidMount() {
        console.log("handler mounted");
        setTimeout(() => {
            this.props.add_voice_sentence('donation_request.wav');
            this.props.add_voice_sentence('Do you want to play white? or black');
        }, 3000);
    }

    componentDidUpdate(prevProps, prevState) {
        console.log("handler updated");
        if (!this.props.fen_position) {
            this.game_handler.new_game();
        }
        else {
            //this breaks the uci commands, better not implement this way
            this.game_handler.load_position(this.props.fen_position);
        }
        if (this.props.player_side) {
            this.game_handler.set_player_side(this.props.player_side);
        }
        let pending_move = this.props.pending_move;
        if (pending_move != null) {
            console.log(pending_move);
            this.game_handler.try_move(pending_move);
        } else {
            this.props.set_position(this.game_handler.game.fen())
        }
        if (this.props.pending_command) {
            this.execute_command(this.props.pending_command);
        }
    }

    set_player_side(side){
        this.props.set_player_side(side);
        if(side == 'white'){
            this.props.add_voice_sentence('You play white.');
        }
    }

    game_help(){
        this.props.add_voice_sentence(`
        The game uses standard algebraic notation, for instance e4, Knight f6, d takes e5, Knight takes e4 or castle kingside
        `);
        this.props.add_voice_sentence(`
        You can say show board to show the game position at any moment for five seconds
        `);
        this.props.add_voice_sentence(`
        say repeat to hear the last move
        `);
        this.props.add_voice_sentence(`
        Say new game when you want to start a new game
        `);
    }

    execute_command(command) {
        let com = command;
        if (com.includes('difficulty')) {
            let difficulty = parseInt(com.split(" ")[1]);
            this.game_handler.set_depth(difficulty);
            this.props.add_voice_sentence('difficulty changed to ' + difficulty.toString());
            this.props.clear_pending_command();
        } else if (com === 'show board') {
            this.refs.board.style.display = 'flex';
            setTimeout(() => {
                this.refs.board.style.display = 'none';
            }, 5000)
            this.props.clear_pending_command();
        } else if (com === 'new game') {
            this.props.set_position(null);
            this.props.clear_pending_command();
        } else if (com === 'white' || com === 'black') {
            this.props.clear_pending_command();
            if (!this.props.player_side) {
                this.set_player_side(com);
            }
        }else if (com === 'repeat') {
            this.repeat();
            this.props.clear_pending_command();
        }else if (com === 'help') {
            this.game_help();
            this.props.clear_pending_command();
        }
    }

    render() {
        let pos = this.props.fen_position;
        if (this.props.fen_position == null) {
            pos = "rnbqkbnr/pppppppp/8/8/8/8/PPPPPPPP/RNBQKBNR w KQkq - 0 1";
        }
        let board_orientation = this.props.player_side ? this.props.player_side : "white";

        return (
            <div ref="board" className="board"
                style={{ display: 'none' }}
            >

                <Chessboard
                    id="humanVsHuman"
                    width={320}
                    position={pos}
                    orientation={board_orientation}
                    boardStyle={{
                        borderRadius: "5px",
                        boxShadow: `0 5px 15px rgba(0, 0, 0, 0.5)`
                    }}
                // onDrop={this.onDrop.bind(this)}
                // onMouseOverSquare={onMouseOverSquare}
                // onMouseOutSquare={onMouseOutSquare}
                // squareStyles={squareStyles}
                // dropSquareStyle={dropSquareStyle}
                // onDragOverSquare={onDragOverSquare}
                // onSquareRightClick={onSquareRightClick}
                // onSquareClick={this.onSquareClick.bind(this)}
                />
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        pending_move: state.game.pending_move,
        pending_command: state.general.pending_command,
        fen_position: state.game.fen_position,
        player_side: state.game.player_side,
    }
};

const mapDispatchToProps = (dispatch) => (
    {
        set_position: (fen_position) => {
            dispatch(set_position(fen_position));
        },
        set_player_side: (player_side) => {
            dispatch(set_player_side(player_side));
        },
        clear_pending_move: () => {
            dispatch(clear_pending_move());
        },
        clear_pending_command: () => {
            dispatch(clear_pending_command());
        },
        try_move: (move_str) => {
            dispatch(try_move(move_str));
        },
        add_voice_sentence: (sentence) => {
            dispatch(add_pending_sentence(sentence));
        },
    });

export default connect(mapStateToProps, mapDispatchToProps)(GameHandlerComponent);

