class MoveToTextConverter {

    constructor(){
        console.log('first sqare for exd5:',this.get_first_square('exd5') );
        
        
    }


    get_first_square(move_str){
        let returned = move_str;
        returned = this.remove_special_chars(returned);
        returned = this.remove_pieces(returned);
        returned = returned.replace('x','');
        returned = returned.slice(0,-2);
        return returned;
    }
    
    get_piece(move_str){
        let returned = "";
        if(move_str.includes('Q')){
            returned = 'queen';
        }
        
        else if(move_str.includes('R')){
            returned = 'rook';
        }
        else if(move_str.includes('B')){
            returned = 'bishop';
        }
        else if(move_str.includes('N')){
            returned = 'knight';
        }
        else if(move_str.includes('K')){
            returned = 'king';
        }
        else if(move_str.includes('O-O') || move_str.includes('O-O-O')){
            returned = 'king';
        }
        else {
            returned = 'pawn';
        }
        
        return returned;
    }

    remove_special_chars(move_str){
        let returned = move_str;
        let special_chars=['+','-',',','.','*','#','=']
        special_chars.forEach(char => {
            returned = returned.split(char).join("");
        });
        return returned;
    }
    remove_pieces(move_str){
        let returned = move_str;
        let special_chars=['Q','B','R','K','N'];
        special_chars.forEach(char => {
            returned = returned.split(char).join("");
        });
        return returned;
    }

    get_takes(move_str){
        if(move_str.includes('x')){
            return 'takes';
        }
        return "";
    }

    get_destination(move_str){
        let final_str = move_str;
        let check_str = '';
        if(move_str.includes('O-O-O')){
            return 'castle queen side';
        }
        if(move_str.includes('O-O')){
            return 'castle king side';
        }
        if(move_str.includes('+')){
            final_str = final_str.split("+").join("");
            check_str = ', check';
        }else if(move_str.includes('#')){
            final_str = final_str.split("#").join("");
            check_str = ', checkmate'
        }

        let final_str_promotion = final_str.split("=");
        if(final_str_promotion.length>1){
            final_str = final_str_promotion[0].substr(-2);
            final_str += ' promotion to '+this.get_piece(final_str_promotion[1]);
        }else{
            final_str = final_str.substr(-2);
        }
        
        return final_str+check_str; 

    }
}
 
export default MoveToTextConverter;