
class Speaker {

  constructor() {
    this.is_ispeaking = false;
    window.speechSynthesis.onvoiceschanged = function (e) {
      this.voice = this.get_voice();
      // console.log(this.voice);
    }.bind(this);
  }

  get_voice(lang) {
    // Fetch the available voices.
    var voices = speechSynthesis.getVoices();
    var available_voices = [];
    // console.log('voices:', voices);
    for (let i = 0; i < voices.length; i++) {

      const voice = voices[i];
      // console.log('checking lang:', voice.lang);
      if (voice.lang === 'en-US') {
        available_voices.push(voice)
      }
    }
    // alert(available_voices.map(emt=>(emt.name)));
    return available_voices[1];
  }

  on_speak_start() { }
  on_speak_end() { }

  speak_started() {
    console.log('speak started');
    this.is_ispeaking = true;
    // this.on_speak_start();
  }

  speak_ended() {
    setTimeout(() => {
      console.log('speak ended');
      this.is_ispeaking = false;
      // this.on_speak_end();
    }, 600);
  }

  // speak(text) {
  //   return new Promise((resolve, reject) => {
  //     console.log('test speaking');
  //     this.speak_ended();
  //     resolve();
  //   });
  // }

  is_audio_file(sentence) {
    if (sentence.includes('.mp3'))
      return true;
    if (sentence.includes('.wav'))
      return true;
    return false;
  }

  speak(text) {
    // Create a new instance of SpeechSynthesisUtterance.
    console.log('speaking:', text);

    return new Promise((resolve, reject) => {

      if (this.is_audio_file(text)) {
        const sound_file = require('./media/'+text);
        let html_audio = document.createElement('audio');
        html_audio.src = sound_file;
        html_audio.onended = function(){
          this.speak_ended();
          resolve();
        }.bind(this);
        html_audio.play();
        this.speak_started();
      } else {

        var msg = new SpeechSynthesisUtterance();

        // Set the text.
        msg.text = text;

        // Set the attributes.
        msg.volume = 1;
        msg.rate = 1;
        msg.pitch = 1;

        // If a voice has been selected, find the voice and set the
        // utterance instance's voice attribute.

        msg.voice = this.voice;
        msg.onend = (() => {
          this.speak_ended();
          resolve();
        }).bind(this);

        // Queue this utterance.
        window.speechSynthesis.speak(msg);
        this.speak_started();
        console.log('waiting for speaking to end');
        // this.wait_until_stop();
      }

    });
  }
}

export default Speaker;




