
import { TRY_MOVE_TYPE } from '../actions/game/try_move'
import { CLEAR_MOVE_TYPE } from '../actions/game/clear_pending_move'
import { SET_POSITION_TYPE } from '../actions/game/set_position'
import { SET_PLAYER_SIDE_TYPE } from '../actions/game/set_player_side'

const default_state = {
    pending_move: null,
    fen_position:null,
    player_side: null,
}

function reducer(state = default_state, action) {


    switch (action.type) {
        case TRY_MOVE_TYPE:
            let pending_move = action.payload;
            return Object.assign({}, state, {
                pending_move,
            })
        case SET_PLAYER_SIDE_TYPE:
            let player_side = action.payload;
            return Object.assign({}, state, {
                player_side,
            })
        case CLEAR_MOVE_TYPE:
            return Object.assign({}, state, {
                pending_move: null,
            })
        case SET_POSITION_TYPE:
            let position = action.payload;
            return Object.assign({}, state, {
                fen_position: position,
                pending_move: null,
            })
        default:
            return state
    }

}

export default reducer;