import React, { Component } from 'react';
import { connect } from 'react-redux';
import { ReactComponent as DonateIcon } from './media/heart.svg';
import Swal from 'sweetalert2';
import add_pending_sentence from '../../redux/actions/voice_interface/add_pending_sentence';
import './style.css';
const finally_audio_url = require('./media/finally.wav');
const finally_audio = new Audio(finally_audio_url);



class Donate extends Component {
    state = {
        show: false
    }

    render_paypal_button(value){
        document.getElementById("ppbtn").innerHTML = "";
        window.paypal.Buttons({
            createOrder: function (data, actions) {
                // This function sets up the details of the transaction, including the amount and line item details.
                return actions.order.create({
                    purchase_units: [{
                        amount: {
                            value
                        }
                    }],
                    application_context: {
                        'shipping_preference': 'NO_SHIPPING'
                    }
                });
            },
            onApprove: function (data, actions) {
                // This function captures the funds from the transaction.
                return actions.order.capture().then(function (details) {
                    // This function shows a transaction success message to your buyer.
                    // alert('Transaction completed by ' + details.payer.name.given_name);
                });
            }
        }).render('#ppbtn');
    }

    clickHandler(event) {
        event.stopPropagation();
        let html_tag = `
        <form class="input-group form-group" autocomplete="off">
            <div class = "input-group-prepend">
                <div class="input-group-text">Select amount (USD)</div>
            </div>
            <input pattern="[0-9]+" type="number" value="2" id="select-price-input" min="1" max="1000" step="1" class="form-control" required>
            <input type="submit" value="select" id="select-price-btn" class="btn btn-primary">
        </form>
        <div id="ppbtn"></div>
        `;

        html_tag = `
        <form class="" autocomplete="off">
            <div class = "form-group text-left">
                <div class="form-check">
                    <input class="form-check-input" type="radio" name="price-select" id="price1" value=2 checked>
                    <label class="form-check-label" for="price1">
                        I liked the project (2 USD)
                    </label>
                </div>
                <div class="form-check">
                    <input class="form-check-input" type="radio" name="price-select" id="price2" value=5>
                    <label class="form-check-label" for="price2">
                        I really loved it (5 USD)
                    </label>
                </div>
                <div class="form-check">
                    <input class="form-check-input" type="radio" name="price-select" id="price3" value=10>
                    <label class="form-check-label" for="price3">
                        I was really waiting for this (10 USD)
                    </label>
                </div>
                <div class="form-check">
                    <input class="form-check-input" type="radio" name="price-select" id="price4" value=30>
                    <label class="form-check-label" for="price4">
                        I'd like to see a future version (30 USD)
                    </label>
                </div>
                <div class="form-check input-group">
                    <div >
                        <input class="form-check-input" type="radio" name="price-select" id="custom-price" value=5>
                        <label class="form-check-label input-group" for="custom-price">
                           <div class="input-group-prepend form-control input-group-text ">Other</div> 
                           <input class="form-control" type="number" id="custom-price-input" value = 5 disabled>
                        </label>
                    </div>
                </div>
            </div>
        </form>
        <div id="ppbtn"></div>
        `;
        const me = this;
        const BSwal = Swal.mixin({
            customClass: {
              confirmButton: 'btn btn-secondary',
              cancelButton: 'btn btn-danger'
            },
            buttonsStyling: false
          })
        BSwal.fire({
            title: "Support the project",
            html: html_tag,
            confirmButtonText: "I can't right now"
        });
        this.render_paypal_button(document.querySelector('input[name="price-select"]:checked').value);
        document.getElementsByClassName("swal2-container")[0].addEventListener('click', function (ev) {
            ev.stopPropagation();
        });
        let radios = document.getElementsByName('price-select');
        for (let i = 0; i < radios.length; i++) {
            const radio = radios[i];
            radio.addEventListener('change', function(){
                if(this.checked){
                    if(this.id == "custom-price"){
                        document.getElementById('custom-price-input').disabled = false;
                        me.render_paypal_button(this.value);
                    }else{
                        me.render_paypal_button(this.value);
                        document.getElementById('custom-price-input').disabled = true;
                    }
                }
            });
        }

        document.getElementById('custom-price-input').addEventListener('input',function(){
            if(!this.value || this.value < 1){
                this.classList.add("is-invalid");
            }else{
                this.classList.remove("is-invalid");
                me.render_paypal_button(this.value);
            }
        });
        // document.getElementById("select-price-btn").addEventListener('click', function (ev) {
        //     ev.preventDefault();
        //     document.getElementById("ppbtn").innerHTML = "";
        //     // let input = document.getElementById("select-price-input");
        //     let input = document.querySelector('input[name="rate"]:checked');
        //     if(!input.value || input.value < 1){
        //         input.classList.add("is-invalid");
        //     }else{
        //         input.classList.remove("is-invalid");
        //         me.render_paypal_button(input.value);
        //     }
        // });
        // finally_audio.play();
    }

    componentDidMount() {
        const me = this;
        document.getElementById('donate-btn').addEventListener('click', function (ev) {
            me.clickHandler(ev);
        });
    }

    componentDidUpdate() {
        const me = this;
        document.getElementById('donate-btn').addEventListener('click', function (ev) {
            me.clickHandler(ev);
        });
    }

    render() {
        return (
            <div>
                <button id="donate-btn" className="btn btn-outline-secondary" >
                    Support my work &nbsp;
                    <DonateIcon className="donate-icon" />
                </button>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
    }
};

const mapDispatchToProps = (dispatch) => (
    {
        add_voice_sentence: (sentence) => {
            dispatch(add_pending_sentence(sentence));
        }
    });


export default connect(mapStateToProps, mapDispatchToProps)(Donate);
