import Chess from "chess.js";


const word_candidates_object = {
    'menu': ['men u', 'man u', 'men you', 'man you'],
    'help': ['help','whelp','helped','hell','helps','hep','health','hil'],
    'yes': ['just', 'yeast','jazz'],
    'no': [],
    'new': [],
    'game': [],
    'repeat': [],
    'difficulty': [],
    '1': ["one"],
    '2': ["too", "to", "two"],
    '3': ["tree", "three"],
    '4': ["for", "forth", 'four', 'pork'],
    '5': ["five", 'fight'],
    'show': ['shows','cho','chau'],
    'board': ['bored','bord','boards','bard'],
    'black': ['Blanc'],
    'white': ['wight','Whyte'],
}

const transform_words_object = {
    'board':'show board',
    'show':'show board',
    'game':'new game',
}


class UICommandsInterpreter {

    constructor(game = null) {


        this.allowed_commands = [
            'no',
            'yes',
            'show board',
            'difficulty 1',
            'difficulty 2',
            'difficulty 3',
            'difficulty 4',
            'difficulty 5',
            'show board',
            'new game',
            'repeat',
            'black',
            'white',
            'help',
        ];

        this.game = new Chess();
        if (game) {
            this.game = game;
        }
    }

    transform_words(str_val){
        let final_str = str_val;
        let arr = final_str.split(" ");
        for (let i = 0; i < arr.length; i++) {
            const word = arr[i];
            if(transform_words_object[word]!=null){
                return transform_words_object[word];
            }
        }
        return final_str;
    }


    interpret(str_val) {
        let final_str = str_val.toLowerCase();
        // console.log('possible_command after toLowerCase:', final_str);
        final_str = this.get_rid_of_signs(final_str);
        // console.log('possible_command after get_rid_of_signs:', final_str);
        final_str = this.replace_words(final_str);
        // console.log('possible_command after replace_words:', final_str);
        final_str = this.remove_remaining(final_str);
        final_str = this.transform_words(final_str);
        if (this.allowed_commands.includes(final_str)){
            return final_str;
        }

        return null;
            // console.log('possible_command after remove_remaining:', final_str);
    }

    get_rid_of_signs(str_val) {
        const signs = ['-', '.', ',', '?', ';', ':'];
        let final_str = str_val;
        for (let i = 0; i < signs.length; i++) {
            const sign = signs[i];
            final_str = final_str.split(sign).join("");
        }
        return final_str;
    }

    replace_all(str_val, search, replace_val) {
        const alias_regexp = new RegExp('\\b' + search + '\\b', 'g')
        return str_val.replace(alias_regexp, replace_val);
    }

    replace_words(str_val) {
        let new_str = str_val;
        const dict = word_candidates_object;
        const keys = Object.keys(dict);
        keys.forEach(key => {
            const alias_array = dict[key];
            alias_array.forEach(alias => {
                new_str = this.replace_all(new_str, alias, key);
            });
        });
        return new_str;
    }

    remove_remaining(str_val) {
        const allowed_words = [
            ...Object.keys(word_candidates_object),
            ...this.allowed_commands
        ]
        let final_words_arr = [];
        const words_arr = str_val.split(" ");
        words_arr.forEach(word => {
            if (allowed_words.includes(word)) {
                final_words_arr.push(word);
            }
        });
        return final_words_arr.join(" ");
    }
}

export default UICommandsInterpreter;